    static getSignDoc = (pubkey, transaction) => {
        const txBodyBytes = this.getTxBodyBytes(transaction);
        const authInfoBytes = this.getAuthInfoBytes(transaction, pubkey);

        const signDoc = makeSignDoc(
            txBodyBytes,
            authInfoBytes,
            transaction.signerData.chainId,
            Number(transaction.signerData.accountNumber),
        );
        return signDoc
    }

    static getCosmosTx = async (address, pubkey, transaction) => {
        // try {
            /* 1. get Account */

            /* 2. make raw transaction */


            /* 3. create signDoc */
            const txBodyBytes = this.getTxBodyBytes(transaction);
            const authInfoBytes = this.getAuthInfoBytes(transaction, pubkey);

            const signDoc = makeSignDoc(
                txBodyBytes,
                authInfoBytes,
                transaction.signerData.chainId,
                Number(transaction.signerData.accountNumber),
            );
            /* 4. serialized singDoc */

            const uint8SignDoc = makeSignBytes(signDoc);
            const serializedTx = `0x${Buffer.from(uint8SignDoc).toString('hex')}`;

            return {
                unSignedTx: signDoc,
                serializedTx,
            };
        // } catch (e) {
        //     console.log(`error : ${e.message}`);
        // }
    };
