    static createCosmosSignedTx = ({ unSignedTx, signature }) => {
        // try {
            const txRaw = TxRaw.fromPartial({
                bodyBytes: unSignedTx.bodyBytes,
                authInfoBytes: unSignedTx.authInfoBytes,
                signatures: [new Uint8Array(Buffer.from(signature.replace('0x', ''), 'hex'))],
            });

            const txByte = TxRaw.encode(txRaw).finish();
            const signedTx = `0x${Buffer.from(txByte).toString('hex')}`;

            return signedTx;
        // } catch (e) {
        //     console.log(`error : ${e.message}`);
        // }
    };
    static getCosmosSignedTx = async (signature, address, pubkey, transaction) => {
        // try {
            /* 1. get rawTransaction */
            const {  unSignedTx } = await this.getCosmosTx(address, pubkey, transaction);
            /* 2. get signature */
            /* 3. create singedTx by combining rawTransaction and signature */
            const cosmosSignedTx = this.createCosmosSignedTx({
                unSignedTx,
                signature: signature,
            });
            return cosmosSignedTx;
        // } catch (e) {
        //     console.log(`error : ${e.message}`);
        // }
    };
    static sendCosmosTransaction = async (cosmosSignedTx) => {
        // try {
            const rpcUrl = 'https://osmosis-rpc.publicnode.com:443';
            const client = await StargateClient.connect(rpcUrl);
            const result = await client.broadcastTx(
                Uint8Array.from(Buffer.from(cosmosSignedTx.replace('0x', ''), 'hex')),
            );
            return result;
        // } catch (e) {
        //     console.log(`error : ${e.message}`);
        // }
    };
